import * as okayapi from '../../../components/okayapi/okayapi.js';


export default {
	data() {
		return {
			title: 'Joke',
			first: '',
		}
	},
	methods: {
		showOkay: function() {
			var params = {
				s: "App.Common_Joke.RandOne", // 必须，待请求的接口服务名称
			};
			uni.request({
				url: this.okayapiHost,
				method: 'GET',
				data: okayapi.enryptData(params),
				success: res => {
					this.first = res.data.data.joke
				},
				fail: res => {
					console.log(res);
				},
				complete: () => {}
			});
		},
	}
}
